//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START

@font-face {
	@include fontdefault(normal, 400, 'Cabin');
	@include fontpath('Cabin-Regular', 'Cabin-Regular', 'Cabin-Regular'); }

@font-face {
	@include fontdefault(normal, 300, 'Circe');
	@include fontpath('Circe-Light', 'Circe-Light', 'Circe-Light'); }

@font-face {
	@include fontdefault(normal, 400, 'Circe');
	@include fontpath('Circe-Regular', 'Circe-Regular', 'Circe-Regular'); }

@font-face {
	@include fontdefault(normal, 700, 'Circe');
	@include fontpath('Circe-Bold', 'Circe-Bold', 'Circe-Bold'); }

@font-face {
	@include fontdefault(normal, 900, 'Circe');
	@include fontpath('Circe-ExtraBold', 'Circe-ExtraBold', 'Circe-ExtraBold'); }
